@import url('https://fonts.googleapis.com/css2?family=Sora:wght@300&display=swap');
@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css");


.sliderimg{
  width: 100%;
  height: 500px;
  object-fit: cover
}

.whatsapp_float {
  position: fixed;
  width: 60px;
  height: 60px;
  bottom: 40px;
  right: 40px;
  background-color: #bce2bc;
  color: #FFF;
  border-radius: 50px;
  text-align: center;
  font-size: 30px;
  box-shadow: 2px 2px 3px #999;
  z-index: 100;
}

.whatsapp-icon {
  margin-top: 16px;
}

/* for mobile */
@media screen and (max-width: 767px) {
  .whatsapp-icon {
      margin-top: 10px;
  }

  .whatsapp_float {
    bottom: 30px;
    font-size: 45px;
    height: 65px;
    /* right: 90px; */
    width: 65px;
}
}